<template>
  <div class="g-mainc g-mainc-formargin">
    <div class="m-content-top">
      <span></span>
    </div>
    <div class="m-content-main">
      <div class="m-content_input">
        <div class="m-table">
          <ul>
            <li class="m-table-head">
              <div class="u-first-column">
                <input type="checkbox"   :checked="isSelectAll">
                <label for="checkall" @click="selectAll(isSelectAll)"></label>
              </div>
              <div class="u-second-column">
                <span class="gray-font"></span>
              </div>
              <div class="u-third-column">
                <span class="u-head-font"></span>
              </div>
              <div class="u-fourth-column">
                <span class="u-head-font"></span>
              </div>
              <div class="u-fifth-column">
                <span class="u-head-font"></span>
              </div>
            </li>
            <div v-for="(item, index) in renderData.tableItems" :key="item.id">
              <div class="u-solid"></div>
              <li class="m-table-info" :class="{'m-table-info':!renderData.showFlag[index],'m-table-info-opened':renderData.showFlag[index]}" >
                <div class="u-first-column">
                  <input
                    type="checkbox"
                    :checked="item.isSelect"
                  >
                  <label for="check" @click="selectChildrenAll(item.isSelect, index)"></label>
                  <!-- <img src="../../../common/img/uncheck.png"> -->
                </div>
                <div class="u-second-column">
                  <span class="u-normal-font">{{item.title}}</span>
                </div>
                <div class="u-third-column">
                  <span class="u-normal-font">{{item.desc}}</span>
                </div>
                <div class="u-fourth-column">
                  <span>{{item.count}}</span>
                </div>
                <div class="u-fifth-column" v-if="renderData.showFlag[index]" @click="showList(index)">
                  <span class="u-open"></span>
                  <img src="../../../common/img/bluedown.png">
                </div>
                <div class="u-fifth-column" v-else @click="showList(index)">
                  <span class="u-close"></span>
                  <img src="../../../common/img/graydown.png">
                </div>
              </li>
              <div v-show="renderData.showFlag[index]">
                <div class="u-solid"></div>
                <li class="m-table-info-isopen" v-for="(item1, index) in item.joblist" :key="index">
                  <div class="u-check-son">
                    <input type="checkbox" :checked="item1.isSelect">
                    <label for="check" @click="item1.isSelect=!item1.isSelect"></label>
                    <!-- <img src="../../../common/img/yellw-check.png"> -->
                  </div>
                  <div class="u-second-column">
                    <span class="u-normal-font u-hover">{{item1.title}}</span>
                  </div>
                  <div class="u-third-column">
                    <span class="u-normal-font">{{item1.desc}}</span>
                  </div>
                  <div class="u-fourth-column">
                    <span>{{item1.count}}</span>
                  </div>
                  <div class="u-fifth-column"></div>
                </li>
              </div>
            </div>
            <div class="u-solid"></div>
            <li class="m-table-info">
              <div class="u-first-column">
                <span class="u-red"></span>
              </div>
              <div class="u-second-column"></div>
              <div class="u-third-column"></div>
              <div class="u-fourth-column">
                <span class="u-red">{{getTotal.total}}</span>
              </div>
              <div class="u-fifth-column"></div>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'roleAddPathTableView',
  props: {
    params: {
      type: Object,
      default: () => []
    }
  },
  components: {
    // handleRoleView
    // removeRoleView
  },
  data () {
    return {
      // // 
      // showFlag: [],
      // items: [
      //   {
      //     title: '',
      //     desc: '1desc',
      //     count: 100,
      //     // checked: false,
      //     joblist: [
      //       {
      //         title: '1',
      //         desc: '1desc',
      //         count: 100
      //       },
      //       {
      //         title: '1',
      //         desc: '1desc',
      //         count: 100
      //       }
      //     ]
      //   },
      //   {
      //     title: '2',
      //     desc: '1desc',
      //     count: 200,
      //     // checked: false,
      //     joblist: [
      //       {
      //         title: '2',
      //         desc: '1desc',
      //         count: 100
      //       },
      //       {
      //         title: '2',
      //         desc: '1desc',
      //         count: 100
      //       }
      //     ]
      //   }
      // ],
      // checkData: []
    }
  },
  computed: {
    renderData () {
      return this.params
    },
    // 
    isSelectAll: function () {
      // itemsisSelecttruetruefalse;
      return this.params.tableItems.every(function (val) { return val.isSelect })
    },
    getTotal: function () {
      let chooseItems = this.params.tableItems.filter(function (val) { return val.isSelect })
      let total = 0
      for (let i = 0, len = chooseItems.length; i < len; i++) {
        total += chooseItems[i].count
      }
      // 
      for (let j in this.params.tableItems) {
        let chooseChildItems = this.params.tableItems[j].joblist.filter(function (val) { return val.isSelect })
        for (let i = 0, len = chooseChildItems.length; i < len; i++) {
          total += chooseChildItems[i].count
        }
      }
      return {total: total}
    }
  },
  methods: {
    // 
    showList (index) {
      // this.$emit('remove')
      this.$set(this.params.showFlag, index, !this.params.showFlag[index])
    },
    // 
    selectAll (_isSelect) {
      // items
      for (let i = 0, len = this.params.tableItems.length; i < len; i++) {
        this.params.tableItems[i].isSelect = !_isSelect
      }
      // 
      for (let j in this.params.tableItems) {
        for (let i = 0, len = this.params.tableItems[j].joblist.length; i < len; i++) {
          this.params.tableItems[j].joblist[i].isSelect = !_isSelect
        }
      }
    },
    // 
    selectChildrenAll (_isSelect, index) {
      this.params.tableItems[index].isSelect = !_isSelect
      // 
      for (let i = 0, len = this.params.tableItems[index].joblist.length; i < len; i++) {
        this.params.tableItems[index].joblist[i].isSelect = !_isSelect
      }
    }

  },
  watch: {
  },
  mounted: function () {
    let _this = this
    // itemsselectfalse
    this.params.tableItems.map(function (item) {
      _this.$set(item, 'isSelect', false)
    })
    for (let i in this.params.tableItems) {
      for (let j in this.params.tableItems[i].joblist) {
        _this.$set(this.params.tableItems[i].joblist[j], 'isSelect', false)
      }
    }
  }

}
</script>
<style lang="scss" scoped>
@import "../../../common/scss/common.scss";
.g-main {
  width: 100%;
  height: auto;
  background-color: #f6f6f6;
  padding-top: 40px;
  padding-bottom: 40px;
  .g-mainc {
    width: 1260px;
    height: 100%;
    border-radius: 10px;
    background-color: #ffffff;
    margin: 0 auto;
    .m-content-top {
      display: flex;
      align-items: center;
      height: 70px;
      border-bottom: 1px solid #d9d9d9;
      padding: 0px 30px;
      height: 60px;
      margin: 0px auto;
      background: #ffffff;
      border-radius: 8px 8px 0px 0px;
      span {
        font-size: 18px;
        color: #333333;
      }
    }
    .m-content-main {
      padding: 30px;
      .m-content_input {
        display: flex;
        align-items: center;
        margin-top: 10px;
        .m-table {
          overflow: hidden;
          width: 100%;
          border: 1px solid #e9e9e9;
          border-radius: 8px;
          border-collapse: collapse;
          text-align: center;
          font-size: 14px;
          .m-table-head {
            display: flex;
            box-sizing: border-box;
            align-items: center;
            direction: row;
            height: 41px;
            background-color: #f8f8f8;
            color: #dadada;
          }
          .m-table-info {
            display: flex;
            box-sizing: border-box;
            align-items: center;
            direction: row;
            height: 60px;
            background-color: #ffffff;
            color: #222;
          }
          .m-table-info-opened {
            display: flex;
            box-sizing: border-box;
            align-items: center;
            direction: row;
            height: 60px;
            background-color: rgba(237, 238, 254, 0.5);
            color: #222;
          }

          .m-table-info-isopen {
            display: flex;
            box-sizing: border-box;
            align-items: center;
            direction: row;
            height: 60px;
            background-color: rgba(255, 244, 229, 0.5);
            color: #222;
          }
          .u-first-column {
            text-align: center;
            flex: 2;
            margin-left: 5px;
            // position: relative;
            input[type="checkbox"] {
              display: none;
            }

            label {
              float: left;
              width: 16px;
              height: 16px;
              vertical-align: middle;
              margin-bottom: 2px;
              cursor: pointer;
              background: url("../../../common/img/uncheck.png") no-repeat;
              background-size: cover;
              margin-left: 40px;
            }

            input[type="checkbox"]:checked + label {
              // background: url("../../../common/img/check.png")no-repeat;
              background: url("data:image/png;base64,");
              background-size: cover;
              margin-left: 40px;
            }
          }
          .u-check-son {
            text-align: center;
            flex: 2;
            margin-left: 5px;
            input[type="checkbox"] {
              display: none;
            }

            label {
              float: left;
              width: 16px;
              height: 16px;
              cursor: pointer;
              background: url("../../../common/img/uncheck.png") no-repeat;
              background-size: cover;
              margin-left: 40px;
            }

            input[type="checkbox"]:checked + label {
              // background: url("../../../common/img/yellow-check.png")no-repeat;
              background: url("data:image/png;base64,");
              background-size: cover;
              margin-left: 40px;
            }
          }
          .u-second-column {
            text-align: center;
            flex: 5;
          }
          .u-third-column {
            text-align: center;
            flex: 12;
            .u-underline {
              text-decoration: underline;
            }
          }
          .u-fourth-column {
            text-align: center;
            flex: 2;
          }
          .u-fifth-column {
            text-align: center;
            flex: 3;
            .u-close {
              color: #999999;
            }
            .u-open {
              color: #404cf6;
            }
            img {
              width: 12px;
              height: 12px;
              padding-left: 10px;
            }
          }

          .u-checkall {
            position: absolute;
            display: inline-block;
            vertical-align: baseline;
            width: 50px;
            top: -10px;
            margin-left: 20px;
          }
          .u-solid {
            border: 1px solid #f6f6f6;
          }
          .u-dashed {
            border: 1px dashed #e9e9e9;
            opacity: 0.5;
          }
          .u-red {
            color: #d11229;
          }
        }
      }
      .m-input-formargin {
        margin-top: 30px;
      }
    }
  }
  .g-mainc-formargin {
    margin-top: 30px;
  }
  .m-buttons {
    width: 1260px;
    height: 50px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-top: 50px;
    padding-bottom: 337px;
    margin-left: auto;
    margin-right: auto;
    div {
      width: 120px;
      height: 50px;
      border: 1px solid #404cf6;
      border-radius: 8px;
      font-size: 18px;
      text-align: center;
      line-height: 50px;
      margin-left: 20px;
    }
    .u-cancel {
      background-color: #ffffff;
      color: #404cf6;
    }
    .u-save {
      background-color: #404cf6;
      color: #ffffff;
    }
  }
}
</style>



