import _ from 'lodash'
import { developinfo, addrole, addrolepath, deleterole, queryRoleById, updateRole } from '../../network/httpService'
const developRoleServ = {
  // 
  state: {
    pageSize: '',
    currentPage: '0',
    totalCount: '',
    totalPage: '',
    datalist: [],
    queryRoleInfo: {
      id: '',
      name: '',
      memo: ''
    },
    pageIndex: '0'
  },
  // 
  actions: {
    'DO_INIT_DATA' ({ state, commit }, res = 0) {
      return new Promise(function (resolve) {
        commit('SET_DATA')
        resolve(true)
      })
    },
    /**
         * @Description:
         * @Author: dongshen
         * @param {type}
         * @return:
         * @Date: 2019-05-15 21:11:12
         */
    'DO_GET_DEVELOP_INFO' ({ state, commit }, value) {
      commit('SET_PAGE_INDEX', value)
      return developinfo(state.pageIndex).then(function (res) {
        return new Promise(function (resolve, reject) {
          console.log(res)
          let { code, data } = res
          let obj = data.returnData.data
          let objlist = data.returnData.datalist
          obj.data = objlist
          // 
          if (code !== 10000) {
            // reject({ msg: code })
            console.log('error')
          } else {
            resolve(commit('SET_DEVELOP_INFO', obj))
          }
        }).catch()
      })

      // 
      // let obj = '{"pageSize":8,"currentPage":1,"totalCount":7,"totalPage":1,"data":[{"workno":null,"roleWayList":[{"currentIndex":null,"roleWayTechVOs":null,"abilityTypeVOs":null,"technologyPointId":null,"abilityTypeIds":null,"id":10,"roleId":6,"name":"1","level":1,"memo":"1","passingScore":null},{"currentIndex":null,"roleWayTechVOs":null,"abilityTypeVOs":null,"technologyPointId":null,"abilityTypeIds":null,"id":11,"roleId":6,"name":"2","level":2,"memo":"2","passingScore":null}],"peaks":"120","memo":"","id":6,"publish":1,"name":""},{"workno":null,"roleWayList":[{"currentIndex":null,"roleWayTechVOs":null,"abilityTypeVOs":null,"technologyPointId":null,"abilityTypeIds":null,"id":7,"roleId":5,"name":"11","level":1,"memo":"","passingScore":null},{"currentIndex":null,"roleWayTechVOs":null,"abilityTypeVOs":null,"technologyPointId":null,"abilityTypeIds":null,"id":8,"roleId":5,"name":"2222222","level":2,"memo":"","passingScore":null},{"currentIndex":null,"roleWayTechVOs":null,"abilityTypeVOs":null,"technologyPointId":null,"abilityTypeIds":null,"id":9,"roleId":5,"name":"3333333","level":3,"memo":"","passingScore":null}],"peaks":"120","memo":"","id":5,"publish":2,"name":""}]}'
      // let data = JSON.parse(obj)
      // console.log(data)
      // commit('SET_DEVELOP_INFO', data)
    },
    /**
         * @Description: 
         * @Author: dongshen
         * @param {type}
         * @return:
         * @Date: 2019-06-11 22:12:31
         */
    'DO_ADD_ROLE' ({ state, commit }, value) {
      return new Promise(function (resolve, reject) {
        let param = value
        addrole(param).then((res) => {
          let { data } = res
          console.log('sucess')
          resolve(commit('SET_DEVELOP_INFO', data.data))
          // let roleList = JSON.parse(JSON.stringify(state.datalist))
          // roleList.splice(0, 1, data)
          // resolve(commit('SET_ADD_ROLE_INFO', { list: roleList }))
        }).catch(
          err => {
            reject(err)
          }
        )
      })
    },
    /**
         * @Description: 
         * @Author: dongshen
         * @param {type}
         * @return:
         * @Date: 2019-06-11 22:12:56
         */
    'DO_ADD_ROLE_PATH' ({ state, commit }, value) {
      return new Promise(function (resolve, reject) {
        let param = value
        addrolepath(param).then((res) => {
          let { code } = res
          // 
          if (code !== 10000) {
            // reject({ msg: code })
            console.log('error')
          } else {
            console.log('sucess')
            // resolve(commit('SET_ADD_ROLE_INFO', data))
          }
        }).catch(
          err => {
            reject(err)
          }
        )
      })
    },
    // 
    'DO_REMOVE_ROLE' ({ state, commit, dispatch, getters }, value) {
      return new Promise(function (resolve, reject) {
        let param = value
        deleterole(param).then((res) => {
          let { code, data } = res
          // 
          if (code === 10000) {
            // reject({ msg: code })
            console.log('error')
          } else {
            console.log('sucess')
            resolve(commit('SET_DEVELOP_INFO', data.data))
          }
        }).catch(
          err => {
            reject(err)
          }
        )
      })
    },
    // id
    'DO_QUERY_ROLE_BY_ID' ({ commit, dispatch, getters }, value) {
      return new Promise(function (resolve, reject) {
        let param = value
        queryRoleById(param).then((res) => {
          let { code, data } = res
          // 
          if (code === 10000) {
            // reject({ msg: code })
            console.log('error')
          } else {
            console.log('sucess')
            resolve(commit('SET_QUERY_ROLE_INFO', data))
          }
        }).catch(
          err => {
            reject(err)
          }
        )
      })
    },
    'DO_UPDATE_ROLE' ({ state, commit }, value) {
      return new Promise(function (resolve, reject) {
        let param = value
        updateRole(param).then((res) => {
          let { data } = res
          console.log('sucess')
          resolve(commit('SET_DEVELOP_INFO', data.data))
        }).catch(
          err => {
            reject(err)
          }
        )
      })
    }
  },
  // 
  mutations: {
    'SET_DEVELOP_INFO' (state, data) {
      state.currentPage = data.currentPage
      state.totalCount = data.totalCount
      state.totalPage = data.totalPage
      state.pageSize = data.pageSize
      state.datalist = []
      if (state.datalist.length === 0) {
        // 
        _.forEach(data.data, function (item) {
          state.datalist.push(item)
        })
      }
    },
    'SET_ADD_ROLE_INFO' (state, res = {}) {
      // 
      state.datalist = []
      _.forEach(res.list, function (item) {
        state.datalist.push(item)
      })
    },
    'SET_ROLE_DATALIST' (state, res = {}) {
      // 
      state.datalist = []
      _.forEach(res, function (item) {
        state.datalist.push(item)
      })
    },
    // id
    'SET_QUERY_ROLE_INFO' (state, res = {}) {
      state.queryRoleInfo.id = res.id
      state.queryRoleInfo.name = res.name
      state.queryRoleInfo.memo = res.memo
    },
    // page
    'SET_PAGE_INDEX' (state, res = {}) {
      state.pageIndex = res
    }

  },
  // 
  getters: {
    /**
         * @Description:
         * @Author: dongshen
         * @param {type}
         * @return:
         * @Date: 2019-05-15 21:13:53
         */
    'GET_DEVELOP_ROLE' (state) {
      return state
    }
  },
  namespaced: true
}
export default developRoleServ



