      // menu: [
      //   { value: '' },
      //   { value: '' },
      //   { value: '' },
      //   { value: '' }
      // ]
    }
  },
  components: {
    logoView,
    menuView,
    userInfoView,
    roleStepView
  },
  computed: {
    renderData () {
      return this.params
    }
  },
  methods: {
    searchOk (value) {
      this.$emit('searchOk', value)
    }

  },
  mounted () {}
}
</script>
<style lang="scss" scoped>
@import "../scss/common.scss";
